body {
  display: flex;
  min-height: 100vh;
  flex-direction: column; }

main {
  padding-top: 30px;
  flex: 1 0 auto; }

form .search {
  border: 2px solid #E57373;
  border-radius: 25px;
  outline: none;
  height: 3rem;
  width: 100%;
  font-size: 1rem;
  margin: 0 0 0 0;
  padding: 5px;
  box-shadow: none;
  box-sizing: content-box;
  transition: all 0.3s;

  &:focus:not([readonly]) {
    border: 2px solid #B75C5C;
    box-shadow: none; } }

.comments {
  height: 300px;
  overflow-y: scroll; }

.logout {
  background: none!important;
  border: none;
  padding-top: 0px;
  font-size: 16px;
  color: #26a69a;
  display: block;
  line-height: 22px;
  padding: 14px 16px;
  &:hover {
    background-color: rgba(0, 0, 0, 0.1); } }
